import React from "react"
import { Box } from "@mui/material"
import { DropFileImage } from "@rp/react/web/common/base-components"
import { useDeviceInfo } from "@rp/react/web/common/hooks"

type FilesListNoUploadedProps = {
  isLoading: boolean
}

const FilesListNoUploaded = ({ isLoading }: FilesListNoUploadedProps) => {
  const { isMobile } = useDeviceInfo()

  return (
    <Box
      sx={{
        height: isMobile ? "calc(100dvh - 180px)" : "100%",
        width: "100%",
      }}
    >
      <DropFileImage isLoading={isLoading} />
    </Box>
  )
}

export { FilesListNoUploaded }
